body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  width: 100%;
  height: 7vh;
  background-color:black;
  position: fixed;
  text-align: center;
  box-shadow: 0px 0px 10px 0px;
  z-index: 1;
  margin-top: -10vh;
}

.header-txt{
  margin-top: 0;
  color: white;
  text-align: center;
  line-height: 7vh;
}

.input-card {
  box-shadow: 1px 1px 10px 1px;
  height: 100%;
  width: 50%;
  margin: auto;
  margin-top: 10vh;
}

.input-fields {
  width: 50%;
  margin: auto;
  margin-bottom: 5%;
  height: 4vh;
}

.mortgage-input {
  width: 35%;
  margin: auto;
  margin-bottom: 5%;
  height: 4vh;
}

.moreDets{
  height: 5vh;
  width: 15%;
  text-align: center;
}

.mortgage-label{
  display: inline-block;
  align-items: right;
}

.mortgage-calculator{
  display: inline;
  width: 35px;
  margin-top: 2%
}

.button {
  background-color: rgb(75, 181, 67);
  color: white;
  height: 5vh;
  width: 20vw;
  margin-top: 2vh;
  margin-bottom: 5vh;
  font-size: 20px;
}
.button:hover {
  height: 5.5vh;
  width: 20.5vw;

}

.resultsCard {
  box-shadow: 1px 1px 10px 1px;
  width: 50%;
  margin: auto;
  margin-top: 5vh;
  height: 30vh;
  margin-bottom: 5vh;
}

.results {
  text-align: left;
  margin-left: 5%;
}

@media screen and (max-width:750px){
  .input-card {
    box-shadow: 1px 1px 10px 1px;
    height: 100%;
    width: 80%;
    margin: auto;
    margin-top: 10vh;
  }
  .resultsCard {
    box-shadow: 1px 1px 10px 1px;
    width: 80%;
    margin: auto;
    margin-top: 5vh;
    height: 30vh;
    margin-bottom: 5vh;
  }
}



